// RsiGptFrontOnlyDemo.jsx
//  FastAPI    
//   (1m, 5m, 10m, 1h, 4h, 1d ...)  RSI 
//    " 10 /RSI//"    
//     ,  API     
//    FastAPI (main.py)  .
//    - GET /api/indicators/latest-rsi?symbol=BTCUSDT&timeframe=1h
//    - GET /api/indicators/recent-candles?symbol=BTCUSDT&timeframe=1h&limit=10

import React, { useState, useEffect } from "react";

// ======  API  URL  ======
const API_BASE =
  import.meta?.env?.VITE_API_BASE_URL ||
  "http://localhost:8000";

//    
const TIMEFRAME_OPTIONS = [
  "1m",
  "5m",
  "10m",
  "15m",
  "30m",
  "1h",
  "4h",
  "1d",
];

//   FastAPI   (1) :  RSI  
async function fetchLatestRsiFromServer(symbol, timeframe) {
  const url =
    `${API_BASE}/api/indicators/latest-rsi` +
    `?symbol=${encodeURIComponent(symbol)}` +
    `&timeframe=${encodeURIComponent(timeframe)}`;

  const res = await fetch(url, {
    method: "GET",
  });

  if (!res.ok) {
    const text = await res.text().catch(() => "");
    console.error("RSI API :", res.status, text);
    throw new Error("RSI API  ");
  }

  const data = await res.json();
  return {
    rsi: data.rsi,
    updatedAt: data.updated_at || data.updatedAt,
  };
}

//   FastAPI   (2) :  N  
async function fetchRecentCandlesFromServer(symbol, timeframe, limit = 10) {
  const url =
    `${API_BASE}/api/indicators/recent-candles` +
    `?symbol=${encodeURIComponent(symbol)}` +
    `&timeframe=${encodeURIComponent(timeframe)}` +
    `&limit=${encodeURIComponent(limit)}`;

  const res = await fetch(url, {
    method: "GET",
  });

  if (!res.ok) {
    const text = await res.text().catch(() => "");
    console.error(" API :", res.status, text);
    throw new Error(" API  ");
  }

  const data = await res.json();
  return data.candles || [];
}

//   ( )
function formatKoreanDateTime(iso) {
  if (!iso) return "-";
  try {
    return new Date(iso).toLocaleString("ko-KR", {
      timeZone: "Asia/Seoul",
      month: "2-digit",
      day: "2-digit",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

const RsiGptFrontOnlyDemo = () => {
  //  
  const [symbol, setSymbol] = useState("BTCUSDT");

  //   RSI  
  const [rsiMap, setRsiMap] = useState({});
  const [selectedTf, setSelectedTf] = useState("1h");

  //    10 
  const [recentCandles, setRecentCandles] = useState([]);

  const [isLoading, setIsLoading] = useState(false);
  const [lastUpdatedAll, setLastUpdatedAll] = useState(null);
  const [error, setError] = useState(null);

  //         
  useEffect(() => {
    refreshAllTimeframesAndCandles();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [symbol, selectedTf]);

  //    RSI +    10  
  const refreshAllTimeframesAndCandles = async () => {
    try {
      setIsLoading(true);
      setError(null);

      // 1)   RSI
      const entries = await Promise.all(
        TIMEFRAME_OPTIONS.map(async (tf) => {
          const data = await fetchLatestRsiFromServer(symbol, tf);
          return [tf, data];
        })
      );
      const nextMap = {};
      entries.forEach(([tf, data]) => {
        nextMap[tf] = data;
      });
      setRsiMap(nextMap);

      // 2)    10 
      const candles = await fetchRecentCandlesFromServer(
        symbol,
        selectedTf,
        10
      );
      setRecentCandles(candles);

      setLastUpdatedAll(new Date().toISOString());
    } catch (e) {
      console.error(e);
      setError(e.message || "RSI/    .");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSelectTimeframe = (tf) => {
    setSelectedTf(tf);
    // selectedTf  useEffect    
  };

  return (
    <div
      style={{
        maxWidth: 900,
        margin: "24px auto",
        padding: 16,
        borderRadius: 16,
        border: "1px solid #e5e7eb",
        boxShadow: "0 8px 20px rgba(15,23,42,0.14)",
        fontFamily: "-apple-system, BlinkMacSystemFont, system-ui, sans-serif",
        background: "#ffffff",
        color: "#111827",
      }}
    >
      {/*  */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          gap: 8,
          alignItems: "center",
          marginBottom: 4,
          flexWrap: "wrap",
        }}
      >
        <h1
          style={{
            fontSize: 20,
            fontWeight: 800,
            margin: 0,
            letterSpacing: "-0.02em",
          }}
        >
            RSI +  10 
        </h1>

        {/* API     */}
        <div
          style={{
            display: "flex",
            alignItems: "center",
            gap: 6,
            fontSize: 11,
          }}
        >
          <span
            style={{
              display: "inline-flex",
              alignItems: "center",
              gap: 4,
              padding: "4px 8px",
              borderRadius: 999,
              backgroundColor: isLoading ? "#eff6ff" : "#f3f4f6",
              color: isLoading ? "#1d4ed8" : "#6b7280",
              border: isLoading
                ? "1px solid #bfdbfe"
                : "1px solid #e5e7eb",
            }}
          >
            <span
              style={{
                width: 6,
                height: 6,
                borderRadius: "999px",
                backgroundColor: isLoading ? "#1d4ed8" : "#9ca3af",
              }}
            />
            {isLoading ? "  ..." : "  "}
          </span>
        </div>
      </div>

      <p
        style={{
          color: "#6b7280",
          fontSize: 12,
          marginBottom: 12,
          lineHeight: 1.5,
        }}
      >
           1m  10m  1h  4h  1d     RSI
          ,
        <br />
          <strong>   10 ( + RSI + )</strong>
          .
      </p>

      {/*   (    API ) */}
      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          gap: 8,
          alignItems: "center",
          marginBottom: 12,
        }}
      >
        <div style={{ flex: "1 1 120px", minWidth: 160 }}>
          <label
            style={{
              fontSize: 11,
              color: "#6b7280",
              display: "block",
              marginBottom: 4,
            }}
          >
             (: BTCUSDT, ETHUSDT)
          </label>
          <input
            value={symbol}
            onChange={(e) => setSymbol(e.target.value)}
            style={{
              width: "100%",
              padding: "6px 8px",
              fontSize: 13,
              borderRadius: 8,
              border: "1px solid #d1d5db",
            }}
          />
        </div>
      </div>

      {/*    () */}
      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          gap: 6,
          marginBottom: 10,
        }}
      >
        {TIMEFRAME_OPTIONS.map((tf) => {
          const active = tf === selectedTf;
          return (
            <button
              key={tf}
              onClick={() => handleSelectTimeframe(tf)}
              style={{
                padding: "6px 10px",
                borderRadius: 999,
                border: "none",
                cursor: "pointer",
                fontSize: 12,
                background: active ? "#111827" : "#f3f4f6",
                color: active ? "#f9fafb" : "#374151",
                opacity: isLoading && !active ? 0.7 : 1,
              }}
            >
              {tf.toUpperCase()}
            </button>
          );
        })}
      </div>

      {/*   */}
      {error && (
        <div
          style={{
            marginBottom: 10,
            padding: 10,
            borderRadius: 10,
            background: "#fef2f2",
            border: "1px solid #fecaca",
            color: "#b91c1c",
            fontSize: 12,
          }}
        >
           {error}
        </div>
      )}

      {/*  RSI  */}
      <div
        style={{
          borderRadius: 12,
          border: "1px solid #e5e7eb",
          background: "#f9fafb",
          padding: 12,
          marginBottom: 14,
        }}
      >
        <div
          style={{
            fontSize: 12,
            color: "#6b7280",
            marginBottom: 8,
            display: "flex",
            justifyContent: "space-between",
            gap: 8,
            flexWrap: "wrap",
          }}
        >
          <span>
            : <strong>{symbol}</strong>
          </span>
          <span>
              :{" "}
            <strong>{formatKoreanDateTime(lastUpdatedAll)}</strong>
          </span>
        </div>

        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(auto-fit, minmax(90px, 1fr))",
            gap: 8,
            opacity: isLoading ? 0.6 : 1,
          }}
        >
          {TIMEFRAME_OPTIONS.map((tf) => {
            const info = rsiMap[tf];
            const isActive = tf === selectedTf;

            // RSI : /  
            let valueColor = "#111827";
            if (info?.rsi >= 70) valueColor = "#b91c1c";
            else if (info?.rsi <= 30) valueColor = "#1d4ed8";

            return (
              <div
                key={tf}
                style={{
                  borderRadius: 10,
                  padding: 8,
                  background: isActive ? "#111827" : "#ffffff",
                  border: isActive
                    ? "1px solid #111827"
                    : "1px solid #e5e7eb",
                  color: isActive ? "#f9fafb" : "#111827",
                  boxShadow: isActive
                    ? "0 4px 10px rgba(15,23,42,0.35)"
                    : "none",
                  transition: "background 0.1s ease, box-shadow 0.1s ease",
                }}
              >
                <div
                  style={{
                    fontSize: 11,
                    color: isActive ? "#e5e7eb" : "#6b7280",
                    marginBottom: 4,
                  }}
                >
                  {tf.toUpperCase()}
                </div>
                <div
                  style={{
                    fontSize: 20,
                    fontWeight: 800,
                    marginBottom: 4,
                    color: isActive ? "#f97316" : valueColor,
                  }}
                >
                  {info ? Number(info.rsi).toFixed(2) : "---"}
                </div>
                <div
                  style={{
                    fontSize: 10,
                    color: isActive ? "#e5e7eb" : "#9ca3af",
                  }}
                >
                  {info
                    ? formatKoreanDateTime(info.updatedAt)
                    : isLoading
                    ? " ..."
                    : " "}
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* :     10   */}
      <div
        style={{
          borderRadius: 12,
          border: "1px solid #e5e7eb",
          background: "#ffffff",
          padding: 12,
        }}
      >
        <div
          style={{
            marginBottom: 8,
            display: "flex",
            justifyContent: "space-between",
            alignItems: "baseline",
            gap: 8,
            flexWrap: "wrap",
          }}
        >
          <div>
            <div
              style={{
                fontSize: 13,
                fontWeight: 700,
                marginBottom: 2,
              }}
            >
               10   {symbol}  {selectedTf.toUpperCase()}
            </div>
            <div
              style={{
                fontSize: 11,
                color: "#6b7280",
              }}
            >
                  RSI          .
            </div>
          </div>
        </div>

        <div
          style={{
            width: "100%",
            overflowX: "auto",
          }}
        >
          <table
            style={{
              width: "100%",
              minWidth: 620,
              borderCollapse: "collapse",
              fontSize: 11,
              opacity: isLoading ? 0.6 : 1,
            }}
          >
            <thead>
              <tr
                style={{
                  background: "#f3f4f6",
                  borderBottom: "1px solid #e5e7eb",
                }}
              >
                <th
                  style={{
                    textAlign: "left",
                    padding: "6px 8px",
                    fontWeight: 600,
                    color: "#4b5563",
                  }}
                >
                  
                </th>
                <th
                  style={{
                    textAlign: "right",
                    padding: "6px 8px",
                    fontWeight: 600,
                    color: "#4b5563",
                  }}
                >
                  
                </th>
                <th
                  style={{
                    textAlign: "right",
                    padding: "6px 8px",
                    fontWeight: 600,
                    color: "#4b5563",
                  }}
                >
                  RSI
                </th>
                <th
                  style={{
                    textAlign: "right",
                    padding: "6px 8px",
                    fontWeight: 600,
                    color: "#4b5563",
                  }}
                >
                  
                </th>
                <th
                  style={{
                    textAlign: "right",
                    padding: "6px 8px",
                    fontWeight: 600,
                    color: "#4b5563",
                  }}
                >
                  
                </th>
                <th
                  style={{
                    textAlign: "right",
                    padding: "6px 8px",
                    fontWeight: 600,
                    color: "#4b5563",
                  }}
                >
                  
                </th>
              </tr>
            </thead>
            <tbody>
              {recentCandles && recentCandles.length > 0 ? (
                recentCandles.map((candle, idx) => {
                  // RSI  
                  let rsiColor = "#111827";
                  if (candle.rsi >= 70) rsiColor = "#b91c1c";
                  else if (candle.rsi <= 30) rsiColor = "#1d4ed8";

                  return (
                    <tr
                      key={idx}
                      style={{
                        borderBottom: "1px solid #f3f4f6",
                        background:
                          idx === 0 ? "#f9fafb" : "transparent",
                      }}
                    >
                      <td
                        style={{
                          padding: "5px 8px",
                          whiteSpace: "nowrap",
                        }}
                      >
                        {formatKoreanDateTime(candle.time)}
                      </td>
                      <td
                        style={{
                          padding: "5px 8px",
                          textAlign: "right",
                          fontWeight: idx === 0 ? 700 : 500,
                        }}
                      >
                        {Number(candle.close).toLocaleString("en-US", {
                          minimumFractionDigits: 2,
                          maximumFractionDigits: 2,
                        })}
                      </td>
                      <td
                        style={{
                          padding: "5px 8px",
                          textAlign: "right",
                          color: rsiColor,
                          fontWeight: idx === 0 ? 800 : 600,
                        }}
                      >
                        {Number(candle.rsi).toFixed(2)}
                      </td>
                      <td
                        style={{
                          padding: "5px 8px",
                          textAlign: "right",
                        }}
                      >
                        {Number(candle.high).toFixed(2)}
                      </td>
                      <td
                        style={{
                          padding: "5px 8px",
                          textAlign: "right",
                        }}
                      >
                        {Number(candle.low).toFixed(2)}
                      </td>
                      <td
                        style={{
                          padding: "5px 8px",
                          textAlign: "right",
                        }}
                      >
                        {Number(candle.volume).toLocaleString("en-US", {
                          minimumFractionDigits: 2,
                          maximumFractionDigits: 2,
                        })}
                      </td>
                    </tr>
                  );
                })
              ) : (
                <tr>
                  <td
                    colSpan={6}
                    style={{
                      padding: "10px 8px",
                      textAlign: "center",
                      color: "#9ca3af",
                    }}
                  >
                    {isLoading
                      ? "    ..."
                      : "   ."}
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      <div
        style={{
          marginTop: 10,
          fontSize: 11,
          color: "#9ca3af",
          lineHeight: 1.4,
        }}
      >
         Tip: FastAPI (main.py)   
               .
        <br />
        /     ,
         <strong>  ...</strong>      .
      </div>
    </div>
  );
};

export default RsiGptFrontOnlyDemo;