import React, { useEffect, useRef } from 'react';
import { 
  createChart, 
  ColorType,
  CandlestickSeries,
  LineSeries
} from 'lightweight-charts';
import { parsePineScript, applyIndicators } from './pineScriptParser';

function LocalChart({ 
  id, 
  symbol, 
  timeframe, 
  pineScript, 
  height = 360,
  onChartReady 
}) {
  const chartContainerRef = useRef(null);
  const chartRef = useRef(null);
  const seriesRef = useRef(null);
  const lineSeriesRefs = useRef([]);

  useEffect(() => {
    if (!chartContainerRef.current) return;

    //   (TradingView )
    const chart = createChart(chartContainerRef.current, {
      layout: {
        background: { type: ColorType.Solid, color: '#131722' },
        textColor: '#d1d4dc',
        fontSize: 12,
      },
      grid: {
        vertLines: { 
          color: '#1e222d',
          style: 0, // 0 = solid
        },
        horzLines: { 
          color: '#1e222d',
          style: 0,
        },
      },
      width: chartContainerRef.current.clientWidth,
      height: height,
      timeScale: {
        timeVisible: true,
        secondsVisible: false,
        borderColor: '#2B2B43',
      },
      rightPriceScale: {
        borderColor: '#2B2B43',
        scaleMargins: {
          top: 0.1,
          bottom: 0.1,
        },
      },
      crosshair: {
        mode: 0, // Normal
        vertLine: {
          color: '#758696',
          width: 1,
          style: 0,
        },
        horzLine: {
          color: '#758696',
          width: 1,
          style: 0,
        },
      },
    });

    chartRef.current = chart;

    //    (TradingView )
    const seriesOptions = {
      upColor: '#26a69a', // TradingView 
      downColor: '#ef5350', // TradingView 
      borderVisible: false,
      wickUpColor: '#26a69a',
      wickDownColor: '#ef5350',
      borderUpColor: '#26a69a',
      borderDownColor: '#ef5350',
    };
    
    // lightweight-charts v5 API: CandlestickSeries import 
    let candlestickSeries;
    
    try {
      if (CandlestickSeries && typeof chart.addSeries === 'function') {
        // v5 API: addSeries(CandlestickSeries, options)
        candlestickSeries = chart.addSeries(CandlestickSeries, seriesOptions);
      } else if (typeof chart.addCandlestickSeries === 'function') {
        //   
        candlestickSeries = chart.addCandlestickSeries(seriesOptions);
      } else {
        throw new Error('CandlestickSeries   . CandlestickSeries:', CandlestickSeries, 'addSeries:', typeof chart.addSeries);
      }
    } catch (error) {
      console.error('[LocalChart]    :', error);
      console.error('[LocalChart] CandlestickSeries:', CandlestickSeries);
      console.error('[LocalChart] chart.addSeries:', typeof chart.addSeries);
      console.error('[LocalChart] chart.addCandlestickSeries:', typeof chart.addCandlestickSeries);
      throw error;
    }

    seriesRef.current = candlestickSeries;

    //  Binance API  
    const fetchBinanceData = async () => {
      try {
        //   (BINANCE:ETHUSDT -> ETHUSDT)
        const binanceSymbol = symbol.includes(':') ? symbol.split(':')[1] : symbol;
        
        //  
        let interval = '1h';
        if (timeframe === 'D') {
          interval = '1d';
        } else if (timeframe === '15') {
          interval = '15m';
        } else if (timeframe === '60') {
          interval = '1h';
        } else if (timeframe === '240') {
          interval = '4h';
        } else {
          interval = `${timeframe}m`;
        }

        // Binance API 
        const limit = 500; //  500 
        const url = `https://api.binance.com/api/v3/klines?symbol=${binanceSymbol}&interval=${interval}&limit=${limit}`;
        
        console.log(`[LocalChart ${id}] Binance API :`, url);
        
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error(`Binance API : ${response.status}`);
        }
        
        const klines = await response.json();
        
        // Binance  : [openTime, open, high, low, close, volume, ...]
        const data = klines
          .map(k => ({
            time: Math.floor(k[0] / 1000), //   
            open: parseFloat(k[1]),
            high: parseFloat(k[2]),
            low: parseFloat(k[3]),
            close: parseFloat(k[4]),
          }))
          .filter(item => 
            item.time > 0 && 
            !isNaN(item.open) && 
            !isNaN(item.high) && 
            !isNaN(item.low) && 
            !isNaN(item.close) &&
            item.high >= item.low &&
            item.high >= Math.max(item.open, item.close) &&
            item.low <= Math.min(item.open, item.close)
          )
          .sort((a, b) => a.time - b.time); //  
        
        console.log(`[LocalChart ${id}] Binance   :`, data.length, ' ');
        if (data.length > 0) {
          console.log(`[LocalChart ${id}]  :`, data[0]);
          console.log(`[LocalChart ${id}]  :`, data[data.length - 1]);
        }
        return data;
      } catch (error) {
        console.error(`[LocalChart ${id}] Binance API :`, error);
        // API      (  )
        return generateFallbackData(symbol);
      }
    };

    // API     
    const generateFallbackData = (sym) => {
      const data = [];
      const now = Date.now();
      const interval = timeframe === 'D' ? 86400000 : parseInt(timeframe) * 60000;
      
      //    
      let basePrice = 3000; // ETH  
      if (sym.includes('BTC')) basePrice = 60000;
      else if (sym.includes('SOL')) basePrice = 150;
      else if (sym.includes('TSLA')) basePrice = 250;
      
      let currentPrice = basePrice;
      
      for (let i = 200; i >= 0; i--) {
        const time = Math.floor((now - i * interval) / 1000);
        //     ( )
        const changePercent = (Math.random() - 0.5) * 0.02; // 1% 
        const open = currentPrice;
        const close = open * (1 + changePercent);
        const high = Math.max(open, close) * (1 + Math.random() * 0.01);
        const low = Math.min(open, close) * (1 - Math.random() * 0.01);
        
        currentPrice = close;
        
        data.push({
          time: time,
          open: Number(open.toFixed(2)),
          high: Number(high.toFixed(2)),
          low: Number(low.toFixed(2)),
          close: Number(close.toFixed(2)),
        });
      }
      
      return data;
    };

    //     
    let validData = [];
    
    //   
    fetchBinanceData().then(data => {
      //  
      validData = data.filter(item => 
        item && 
        item.time != null && 
        !isNaN(item.open) && 
        !isNaN(item.high) && 
        !isNaN(item.low) && 
        !isNaN(item.close) &&
        item.high >= item.low &&
        item.high >= Math.max(item.open, item.close) &&
        item.low <= Math.min(item.open, item.close)
      );
      
      if (validData.length > 0) {
        candlestickSeries.setData(validData);
        
        // Pine Script  
        if (pineScript && pineScript.trim()) {
          setTimeout(() => {
            try {
              const indicators = parsePineScript(pineScript);
              if (indicators.length > 0) {
                const result = applyIndicators(validData, indicators);
                
                //    
                lineSeriesRefs.current.forEach(lineSeries => {
                  try {
                    chartRef.current.removeSeries(lineSeries);
                  } catch (e) {}
                });
                lineSeriesRefs.current = [];

                //    
                result.lines.forEach((lineConfig, idx) => {
                  try {
                    if (!lineConfig.data || lineConfig.data.length === 0) return;
                    
                    const cleanedData = lineConfig.data
                      .filter(item => item && item.time != null && item.value != null && !isNaN(item.value))
                      .map(item => ({
                        time: item.time,
                        value: Number(item.value)
                      }))
                      .filter(item => !isNaN(item.value) && isFinite(item.value));

                    if (cleanedData.length === 0) return;

                    let lineSeries;
                    if (LineSeries && typeof chartRef.current.addSeries === 'function') {
                      lineSeries = chartRef.current.addSeries(LineSeries, {
                        color: lineConfig.color || '#60a5fa',
                        lineWidth: lineConfig.lineWidth || 1,
                        title: lineConfig.title || `Indicator ${idx + 1}`,
                      });
                    } else if (typeof chartRef.current.addLineSeries === 'function') {
                      lineSeries = chartRef.current.addLineSeries({
                        color: lineConfig.color || '#60a5fa',
                        lineWidth: lineConfig.lineWidth || 1,
                        title: lineConfig.title || `Indicator ${idx + 1}`,
                      });
                    } else {
                      return;
                    }
                    
                    lineSeries.setData(cleanedData);
                    lineSeriesRefs.current.push(lineSeries);
                  } catch (e) {
                    console.error(`[LocalChart ${id}]     :`, e);
                  }
                });
                
                //   (BUY/SELL )
                if (result.markers && result.markers.length > 0) {
                  try {
                    console.log(`[LocalChart ${id}]   :`, result.markers.length, '');
                    //     
                    seriesRef.current.setMarkers(result.markers);
                    console.log(`[LocalChart ${id}]   :`, result.markers);
                  } catch (e) {
                    console.error(`[LocalChart ${id}]   :`, e);
                    console.error(`[LocalChart ${id}]  :`, result.markers);
                  }
                } else {
                  console.log(`[LocalChart ${id}]  `);
                }
              }
            } catch (error) {
              console.error(`[LocalChart ${id}]  Pine Script  :`, error);
            }
          }, 200);
        }
      } else {
        console.error('[LocalChart]    ');
      }
    }).catch(error => {
      console.error(`[LocalChart ${id}]   :`, error);
    });


    //  
    const handleResize = () => {
      if (chartContainerRef.current && chartRef.current) {
        chartRef.current.applyOptions({
          width: chartContainerRef.current.clientWidth,
        });
      }
    };

    window.addEventListener('resize', handleResize);

    if (onChartReady) {
      onChartReady(chart);
    }

    return () => {
      window.removeEventListener('resize', handleResize);
      if (chartRef.current) {
        chartRef.current.remove();
      }
    };
  }, [id, symbol, timeframe, height, onChartReady, pineScript]);

  // Pine Script     (   )
  useEffect(() => {
    if (!chartRef.current || !seriesRef.current) {
      console.log(`[LocalChart ${id}]    `);
      return;
    }

    // pineScript    
    if (!pineScript || pineScript.trim() === "") {
      console.log(`[LocalChart ${id}] Pine Script  -  `);
      lineSeriesRefs.current.forEach(lineSeries => {
        try {
          chartRef.current.removeSeries(lineSeries);
        } catch (e) {
          console.warn(`[LocalChart ${id}]   :`, e);
        }
      });
      lineSeriesRefs.current = [];
      return;
    }

    console.log(`[LocalChart ${id}] Pine Script  :`, pineScript.substring(0, 50) + '...');

    try {
      const indicators = parsePineScript(pineScript);
      console.log(`[LocalChart ${id}]  :`, indicators);
      
      if (indicators.length === 0) {
        console.warn(`[LocalChart ${id}]    `);
        //    
        lineSeriesRefs.current.forEach(lineSeries => {
          try {
            chartRef.current.removeSeries(lineSeries);
          } catch (e) {
            console.warn(`[LocalChart ${id}]   :`, e);
          }
        });
        lineSeriesRefs.current = [];
        return;
      }

      //    
      const currentData = seriesRef.current.data();
      console.log(`[LocalChart ${id}]   :`, currentData ? currentData.length : 0);
      
      if (!currentData || currentData.length === 0) {
        console.warn(`[LocalChart ${id}]   `);
        return;
      }

      const result = applyIndicators(currentData, indicators);
      console.log(`[LocalChart ${id}]   :`, result.lines.length);

      //    
      lineSeriesRefs.current.forEach(lineSeries => {
        try {
          chartRef.current.removeSeries(lineSeries);
        } catch (e) {
          console.warn(`[LocalChart ${id}]   :`, e);
        }
      });
      lineSeriesRefs.current = [];

      //    
      result.lines.forEach((lineConfig, idx) => {
        try {
          //    
          if (!lineConfig.data || lineConfig.data.length === 0) {
            console.warn(`[LocalChart ${id}]  ${idx + 1}  `);
            return;
          }

          //     
          const cleanedData = lineConfig.data
            .filter(item => item && item.time != null && item.value != null && !isNaN(item.value))
            .map(item => ({
              time: typeof item.time === 'number' ? item.time : item.time,
              value: Number(item.value)
            }))
            .filter(item => !isNaN(item.value) && isFinite(item.value));

          if (cleanedData.length === 0) {
            console.warn(`[LocalChart ${id}]  ${idx + 1}   `);
            return;
          }

          // v5 API: LineSeries 
          let lineSeries;
          if (LineSeries && typeof chartRef.current.addSeries === 'function') {
            lineSeries = chartRef.current.addSeries(LineSeries, {
              color: lineConfig.color || '#60a5fa',
              lineWidth: lineConfig.lineWidth || 1,
              title: lineConfig.title || `Indicator ${idx + 1}`,
            });
          } else if (typeof chartRef.current.addLineSeries === 'function') {
            lineSeries = chartRef.current.addLineSeries({
              color: lineConfig.color || '#60a5fa',
              lineWidth: lineConfig.lineWidth || 1,
              title: lineConfig.title || `Indicator ${idx + 1}`,
            });
          } else {
            throw new Error('LineSeries   ');
          }

          lineSeries.setData(cleanedData);
          lineSeriesRefs.current.push(lineSeries);
          console.log(`[LocalChart ${id}]   ${idx + 1}  ,  :`, cleanedData.length);
        } catch (e) {
          console.error(`[LocalChart ${id}]    :`, e);
        }
      });
      
      //   (plotshape) - BUY/SELL 
      if (result.markers && result.markers.length > 0) {
        try {
          console.log(`[LocalChart ${id}]   :`, result.markers.length, '');
          //      
          seriesRef.current.setMarkers([]);
          seriesRef.current.setMarkers(result.markers);
          console.log(`[LocalChart ${id}]  ${result.markers.length}  :`, result.markers);
        } catch (e) {
          console.error(`[LocalChart ${id}]   :`, e);
          console.error(`[LocalChart ${id}]  :`, result.markers);
        }
      } else {
        //     
        try {
          seriesRef.current.setMarkers([]);
        } catch (e) {
          // 
        }
      }
      
      console.log(`[LocalChart ${id}] Pine Script  `);
    } catch (error) {
      console.error(`[LocalChart ${id}] Pine Script  :`, error);
    }
  }, [pineScript, id, symbol, timeframe]);

  return (
    <div 
      ref={chartContainerRef} 
      style={{ 
        width: '100%', 
        height: `${height}px`,
        position: 'relative'
      }} 
    />
  );
}

export default LocalChart;

