// TradingViewAIDashboard.jsx
import React, { useEffect, useState, useRef } from "react";
import LocalChart from "./LocalChart";
import { parsePineScript } from "./pineScriptParser";
import RsiGptFrontOnlyDemo from "./RsiGptFrontOnlyDemo";

// 4   ID
const CHART_IDS = [
  "tradingview_chart_1",
  "tradingview_chart_2",
  "tradingview_chart_3",
  "tradingview_chart_4",
];

const TIMEFRAMES = [
  { label: "15m", value: "15" },
  { label: "1H", value: "60" },
  { label: "4H", value: "240" },
  { label: "1D", value: "D" },
];

//   (   )
const DEFAULT_SYMBOLS = [
  "BINANCE:ETHUSDT",
  "BINANCE:BTCUSDT",
  "BINANCE:SOLUSDT",
  "NASDAQ:TSLA",
];

function TradingViewAIDashboard() {
  //  ( )
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (typeof window === "undefined") return;
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };
    checkMobile();
    window.addEventListener("resize", checkMobile);
    return () => window.removeEventListener("resize", checkMobile);
  }, []);

  //   (state)
  const [chartConfigs, setChartConfigs] = useState(
    CHART_IDS.map((_, idx) => ({
      symbol: DEFAULT_SYMBOLS[idx] || DEFAULT_SYMBOLS[0],
      timeframe: "60",
    }))
  );

  //   Pine Script 
  const [pineScripts, setPineScripts] = useState(
    CHART_IDS.map(
      () =>
        `//@version=5
indicator("ETH SuperTrend Signals", overlay=true)

len = input.int(10), factor = input.float(3.0, step=0.1)

[st, dir] = ta.supertrend(factor, len)

long  = dir ==  1 and dir[1] !=  1
short = dir == -1 and dir[1] != -1

plot(st, "SuperTrend", color = dir==1? color.green: color.red)
plotshape(long,  style=shape.triangleup,   location=location.belowbar, color=color.green, text="BUY")
plotshape(short, style=shape.triangledown, location=location.abovebar, color=color.red,   text="SELL")

// ( JSON,   )
alertcondition(long,  "BUY",  '{"side":"buy","symbol":"{{ticker}}","price":{{close}},"time":"{{time}}"}')
alertcondition(short, "SELL", '{"side":"sell","symbol":"{{ticker}}","price":{{close}},"time":"{{time}}"}')`
    )
  );

  const [selectedChartIdx, setSelectedChartIdx] = useState(0);
  const [customSymbol, setCustomSymbol] = useState("");
  const [analysis, setAnalysis] = useState(null);
  const [error, setError] = useState(null);
  const [showRsiView, setShowRsiView] = useState(false);

  //    (tradingview | local)
  const [chartModes, setChartModes] = useState(
    CHART_IDS.map(() => "tradingview")
  );

  //    Pine Script ( )
  const [appliedPineScripts, setAppliedPineScripts] = useState(
    CHART_IDS.map(() => "")
  );

  // TradingView   ref
  const widgetRefs = useRef([]);

  //    touch ref
  const touchStartXRef = useRef(null);
  const touchEndXRef = useRef(null);

  const handleTouchStart = (e) => {
    if (!isMobile) return;
    touchStartXRef.current = e.touches[0].clientX;
  };

  const handleTouchMove = (e) => {
    if (!isMobile) return;
    touchEndXRef.current = e.touches[0].clientX;
  };

  const handleTouchEnd = () => {
    if (!isMobile) return;
    if (touchStartXRef.current === null || touchEndXRef.current === null) return;

    const diff = touchStartXRef.current - touchEndXRef.current;
    const threshold = 50; //   (px)

    if (Math.abs(diff) > threshold) {
      if (diff > 0) {
        //     
        setSelectedChartIdx((prev) => (prev + 1) % CHART_IDS.length);
      } else {
        //     
        setSelectedChartIdx((prev) =>
          prev === 0 ? CHART_IDS.length - 1 : prev - 1
        );
      }
    }

    touchStartXRef.current = null;
    touchEndXRef.current = null;
  };

  // TradingView   (   )
  useEffect(() => {
    if (typeof window === "undefined") return;

    // TradingView   
    const tradingViewIndices = chartModes
      .map((mode, idx) => (mode === "tradingview" ? idx : -1))
      .filter((idx) => idx !== -1);

    if (tradingViewIndices.length === 0) return;

    const scriptId = "tradingview-widget-script";
    let resizeTimer = null;
    let scriptAppended = false;

    const createWidgets = (attempt = 1) => {
      if (typeof window.TradingView === "undefined") {
        setError("TradingView   .");
        return;
      }
      setError(null);

      tradingViewIndices.forEach((idx) => {
        const id = CHART_IDS[idx];
        const container = document.getElementById(id);
        if (!container) {
          setError((prev) =>
            prev ? prev + `\n ${id} ` : ` ${id} `
          );
          return;
        }

        try {
          container.innerHTML = "";

          const cfg = chartConfigs[idx] || {};

          // autosize ,  div  
          const widget = new window.TradingView.widget({
            symbol: cfg.symbol || DEFAULT_SYMBOLS[0],
            interval: cfg.timeframe || "60",
            timezone: "Asia/Seoul",
            theme: "dark",
            style: "1",
            locale: "kr",
            container_id: id,
            autosize: true,
            allow_symbol_change: false,
            hide_top_toolbar: false,
            hide_side_toolbar: false,
            save_image: false,
            overrides: {},
          });

          widgetRefs.current[idx] = widget;
        } catch (e) {
          console.error("[TV DEBUG] widget  :", e);
          setError((prev) =>
            prev
              ? prev + `\nwidget  : ${e.message}`
              : `widget  : ${e.message}`
          );
        }
      });
    };

    const onResize = () => {
      if (resizeTimer) clearTimeout(resizeTimer);
      resizeTimer = setTimeout(() => {
        createWidgets();
      }, 200);
    };

    if (!document.getElementById(scriptId)) {
      const script = document.createElement("script");
      script.id = scriptId;
      script.src = "https://s3.tradingview.com/tv.js";
      script.type = "text/javascript";
      script.async = true;
      script.onload = () => {
        setTimeout(() => createWidgets(1), 300);
      };
      script.onerror = (err) => {
        console.error("[TV DEBUG] tv.js  ", err);
        setError("TradingView    (/CSP )");
      };
      document.body.appendChild(script);
      scriptAppended = true;
    } else {
      setTimeout(() => createWidgets(1), 100);
    }

    window.addEventListener("resize", onResize);

    return () => {
      window.removeEventListener("resize", onResize);
      if (resizeTimer) clearTimeout(resizeTimer);

      widgetRefs.current.forEach((w, i) => {
        try {
          if (w && typeof w.remove === "function") {
            w.remove();
          } else {
            const el = document.getElementById(CHART_IDS[i]);
            if (el) el.innerHTML = "";
          }
        } catch (_) {}
      });
      widgetRefs.current = [];

      if (scriptAppended) {
        const s = document.getElementById(scriptId);
        if (s && s.parentNode) s.parentNode.removeChild(s);
      }
    };
  }, [chartConfigs, chartModes]);

  const handleRunAnalysis = () => {
    setError(null);
    setShowRsiView(true);
  };

  const handleApplyCustomSymbol = () => {
    if (customSymbol.trim()) {
      setChartConfigs((prev) => {
        const next = [...prev];
        next[selectedChartIdx] = {
          ...next[selectedChartIdx],
          symbol: customSymbol.trim().toUpperCase(),
        };
        return next;
      });
      setCustomSymbol("");
    }
  };

  const handleDownloadPine = (idx = selectedChartIdx) => {
    const content = pineScripts[idx] || "";
    const blob = new Blob([content], { type: "text/plain;charset=utf-8" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${CHART_IDS[idx]}_script.pine`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  };

  const handleCopyPineToClipboard = async (idx = selectedChartIdx) => {
    try {
      const text = pineScripts[idx] || "";
      await navigator.clipboard.writeText(text);
      alert(
        "Pine   . TradingView Pine Editor ."
      );
      const cfg = chartConfigs[idx];
      const sym = encodeURIComponent(cfg.symbol);
      const interval = encodeURIComponent(
        cfg.timeframe === "D" ? "D" : `${cfg.timeframe}`
      );
      window.open(
        `https://www.tradingview.com/chart/?symbol=${sym}&interval=${interval}`,
        "_blank"
      );
    } catch (e) {
      console.error(e);
      alert("  .  .");
    }
  };

  const handleChangePine = (value) => {
    setPineScripts((prev) => {
      const next = [...prev];
      next[selectedChartIdx] = value;
      return next;
    });
  };

  const handleApplyPineScript = () => {
    if (chartModes[selectedChartIdx] === "local") {
      const script = pineScripts[selectedChartIdx] || "";
      const indicators = parsePineScript(script);

      if (indicators.length > 0) {
        setAppliedPineScripts((prev) => {
          const next = [...prev];
          next[selectedChartIdx] = script;
          return next;
        });
        alert(
          ` ${indicators.length}  !\n\n :\n${indicators
            .map(
              (ind, idx) =>
                `${idx + 1}. ${ind.type}${
                  ind.period ? `(${ind.period})` : ""
                }`
            )
            .join("\n")}`
        );
      } else {
        alert(
          "      .\n\n :\n- plot(close)\n- plot(sma(close, 20))\n- plot(ema(close, 12))\n- plot(rsi(close, 14))"
        );
      }
    } else {
      alert(
        "    Pine Script   .\n\n TradingView      ."
      );
    }
  };

  const handleLoadExampleScript = () => {
    const examples = [
      `//@version=5
indicator(" ", overlay=true)
plot(close)`,
      `//@version=5
indicator("SMA ", overlay=true)
plot(close)
plot(sma(close, 20))`,
      `//@version=5
indicator("EMA ", overlay=true)
plot(close)
plot(ema(close, 12))`,
      `//@version=5
indicator("RSI ", overlay=true)
plot(close)
plot(rsi(close, 14))`,
      `//@version=5
indicator(" ", overlay=true)
plot(close)
plot(sma(close, 20))
plot(ema(close, 12))`,
    ];
    const randomExample =
      examples[Math.floor(Math.random() * examples.length)];
    setPineScripts((prev) => {
      const next = [...prev];
      next[selectedChartIdx] = randomExample;
      return next;
    });
  };

  const handleLoadSuperTrendScript = () => {
    const superTrendScript = `//@version=5
indicator("ETH SuperTrend Signals", overlay=true)

len = input.int(10), factor = input.float(3.0, step=0.1)

[st, dir] = ta.supertrend(factor, len)

long  = dir ==  1 and dir[1] !=  1
short = dir == -1 and dir[1] != -1

plot(st, "SuperTrend", color = dir==1? color.green: color.red)
plotshape(long,  style=shape.triangleup,   location=location.belowbar, color=color.green, text="BUY")
plotshape(short, style=shape.triangledown, location=location.abovebar, color=color.red,   text="SELL")

// ( JSON,   )
alertcondition(long,  "BUY",  '{"side":"buy","symbol":"{{ticker}}","price":{{close}},"time":"{{time}}"}')
alertcondition(short, "SELL", '{"side":"sell","symbol":"{{ticker}}","price":{{close}},"time":"{{time}}"}')`;
    setPineScripts((prev) => {
      const next = [...prev];
      next[selectedChartIdx] = superTrendScript;
      return next;
    });
  };

  const copyPineToAll = () => {
    setPineScripts((prev) => prev.map(() => prev[selectedChartIdx] || ""));
  };

  const updateSelectedChartConfig = (patch) => {
    setChartConfigs((prev) => {
      const next = [...prev];
      next[selectedChartIdx] = { ...next[selectedChartIdx], ...patch };
      return next;
    });
  };

  const chartBoxStyle = {
    width: "100%",
    height: isMobile ? "260px" : "360px",
    border: "1px solid #1f2937",
    borderRadius: "8px",
    overflow: "hidden",
    background: "#020617",
  };

  const toolbarStyle = {
    marginBottom: 12,
    display: "flex",
    gap: 8,
    alignItems: "center",
    flexWrap: "wrap",
  };

  const labelStyle = { fontSize: 14, color: "#e5e7eb" };

  // PC  grid
  const gridStyleDesktop = {
    display: "grid",
    gridTemplateColumns: "repeat(2, 1fr)",
    gap: "12px",
    alignItems: "stretch",
  };

  return (
    <div
      style={{
        padding: 12,
        background: "#020617",
        minHeight: "100dvh",
        color: "#e5e7eb",
        boxSizing: "border-box",
      }}
    >
      {/*  Pine Script Editor */}
      <div style={{ marginBottom: 12 }}>
        <textarea
          value={pineScripts[selectedChartIdx]}
          onChange={(e) => handleChangePine(e.target.value)}
          placeholder={`//@version=5
indicator("My Indicator", overlay=true)
plot(close)
plot(sma(close, 20))`}
          style={{
            width: "100%",
            minHeight: 160,
            maxHeight: 260,
            padding: 12,
            background: "#0b1220",
            color: "#e5e7eb",
            borderRadius: 6,
            border: "1px solid #1f2937",
            fontFamily: "monospace",
            fontSize: 13,
            resize: "vertical",
          }}
        />
      </div>

      {/*  */}
      <div style={toolbarStyle}>
        <div style={labelStyle}> : #{selectedChartIdx + 1}</div>

        <select
          value={chartModes[selectedChartIdx] || "tradingview"}
          onChange={(e) => {
            setChartModes((prev) => {
              const next = [...prev];
              next[selectedChartIdx] = e.target.value;
              return next;
            });
          }}
          style={{ height: 36, borderRadius: 6 }}
        >
          <option value="tradingview">TradingView</option>
          <option value="local">  (Pine Script )</option>
        </select>

        <select
          value={chartConfigs[selectedChartIdx]?.timeframe || "60"}
          onChange={(e) =>
            updateSelectedChartConfig({ timeframe: e.target.value })
          }
          style={{ height: 36, borderRadius: 6 }}
        >
          {TIMEFRAMES.map((t) => (
            <option key={t.value} value={t.value}>
              {t.label}
            </option>
          ))}
        </select>

        <select
          value={chartConfigs[selectedChartIdx]?.symbol || DEFAULT_SYMBOLS[0]}
          onChange={(e) => updateSelectedChartConfig({ symbol: e.target.value })}
          style={{ height: 36, borderRadius: 6, maxWidth: 180 }}
        >
          {DEFAULT_SYMBOLS.map((s) => (
            <option key={s} value={s}>
              {s}
            </option>
          ))}
        </select>

        <input
          placeholder="  (: BINANCE:ADAUSDT)"
          value={customSymbol}
          onChange={(e) => setCustomSymbol(e.target.value)}
          style={{
            minWidth: 160,
            height: 36,
            padding: "6px 10px",
            borderRadius: 6,
            border: "1px solid #374151",
            background: "#020617",
            color: "#e5e7eb",
            flex: 1,
          }}
        />
        <button
          onClick={handleApplyCustomSymbol}
          style={{
            padding: "8px 12px",
            borderRadius: 6,
            border: "none",
            background: "#4b5563",
            color: "#fff",
            cursor: "pointer",
          }}
        >
          
        </button>

        <button
          onClick={handleRunAnalysis}
          style={{
            padding: "8px 14px",
            borderRadius: 6,
            border: "none",
            background: "#3b82f6",
            color: "#fff",
            cursor: "pointer",
            fontWeight: 500,
          }}
        >
          AI  
        </button>
      </div>

      {/* Pine Script   */}
      <div
        style={{
          marginBottom: 12,
          display: "flex",
          gap: 8,
          flexWrap: "wrap",
          alignItems: "center",
        }}
      >
        <button
          onClick={handleApplyPineScript}
          style={{
            padding: "8px 16px",
            background:
              chartModes[selectedChartIdx] === "local" ? "#10b981" : "#3b82f6",
            color: "#fff",
            border: "none",
            borderRadius: 6,
            cursor: "pointer",
            fontWeight: 500,
            fontSize: 13,
          }}
        >
          {chartModes[selectedChartIdx] === "local"
            ? " Pine Script "
            : "  "}
        </button>
        <button
          onClick={handleLoadSuperTrendScript}
          style={{
            padding: "8px 16px",
            background: "#8b5cf6",
            color: "#fff",
            border: "none",
            borderRadius: 6,
            cursor: "pointer",
            fontWeight: 500,
            fontSize: 13,
          }}
        >
           SuperTrend  
        </button>
        <button
          onClick={handleLoadExampleScript}
          style={{
            padding: "8px 16px",
            background: "#1f2937",
            color: "#e5e7eb",
            border: "1px solid #374151",
            borderRadius: 6,
            cursor: "pointer",
            fontWeight: 500,
            fontSize: 13,
          }}
        >
             
        </button>
        <button
          onClick={copyPineToAll}
          style={{
            padding: "8px 16px",
            background: "#111827",
            color: "#e5e7eb",
            border: "1px solid #374151",
            borderRadius: 6,
            cursor: "pointer",
            fontSize: 12,
          }}
        >
              
        </button>
        <button
          onClick={() => handleDownloadPine(selectedChartIdx)}
          style={{
            padding: "8px 16px",
            background: "#0f172a",
            color: "#e5e7eb",
            border: "1px solid #1f2937",
            borderRadius: 6,
            cursor: "pointer",
            fontSize: 12,
          }}
        >
             
        </button>
        <button
          onClick={() => handleCopyPineToClipboard(selectedChartIdx)}
          style={{
            padding: "8px 16px",
            background: "#6b21a8",
            color: "#fff",
            border: "none",
            borderRadius: 6,
            cursor: "pointer",
            fontSize: 12,
          }}
        >
          TradingView   
        </button>
      </div>

      {/* /PC     */}
      {isMobile ? (
        <>
          {/* :   +    +   */}
          <div
            style={{
              display: "flex",
              gap: 4,
              marginBottom: 8,
              overflowX: "auto",
            }}
          >
            {CHART_IDS.map((_, idx) => (
              <button
                key={idx}
                onClick={() => setSelectedChartIdx(idx)}
                style={{
                  flexShrink: 0,
                  padding: "6px 10px",
                  borderRadius: 999,
                  border: "none",
                  cursor: "pointer",
                  fontSize: 12,
                  background:
                    idx === selectedChartIdx ? "#3b82f6" : "#111827",
                  color: idx === selectedChartIdx ? "#fff" : "#e5e7eb",
                }}
              >
                #{idx + 1}{" "}
                {chartConfigs[idx]?.symbol.replace("BINANCE:", "") ??
                  DEFAULT_SYMBOLS[idx]}
              </button>
            ))}
          </div>

          <div
            style={{ ...chartBoxStyle }}
            onTouchStart={handleTouchStart}
            onTouchMove={handleTouchMove}
            onTouchEnd={handleTouchEnd}
          >
            {chartModes[selectedChartIdx] === "local" ? (
              <LocalChart
                id={CHART_IDS[selectedChartIdx]}
                symbol={
                  chartConfigs[selectedChartIdx]?.symbol ||
                  DEFAULT_SYMBOLS[0]
                }
                timeframe={
                  chartConfigs[selectedChartIdx]?.timeframe || "60"
                }
                pineScript={appliedPineScripts[selectedChartIdx]}
                height={260}
              />
            ) : (
              <div
                id={CHART_IDS[selectedChartIdx]}
                style={{ width: "100%", height: "100%" }}
              />
            )}
          </div>

          <div
            style={{
              marginTop: 6,
              fontSize: 12,
              color: "#9ca3af",
              textAlign: "center",
            }}
          >
             /      .
          </div>
        </>
      ) : (
        // PC/:  22 
        <div style={gridStyleDesktop}>
          {CHART_IDS.map((id, idx) => (
            <div
              key={id}
              style={{
                ...chartBoxStyle,
                cursor: "pointer",
                outline:
                  idx === selectedChartIdx
                    ? "2px solid #60a5fa"
                    : "none",
                position: "relative",
              }}
              onClick={() => setSelectedChartIdx(idx)}
              title={` : ${idx + 1} ( Pine Editor  )`}
            >
              {chartModes[idx] === "local" ? (
                <LocalChart
                  id={id}
                  symbol={chartConfigs[idx]?.symbol || DEFAULT_SYMBOLS[0]}
                  timeframe={chartConfigs[idx]?.timeframe || "60"}
                  pineScript={appliedPineScripts[idx]}
                  height={360}
                />
              ) : (
                <div id={id} style={{ width: "100%", height: "100%" }} />
              )}
              <div
                style={{
                  position: "absolute",
                  top: 8,
                  right: 8,
                  padding: "4px 8px",
                  background:
                    chartModes[idx] === "local" ? "#10b981" : "#3b82f6",
                  color: "#fff",
                  borderRadius: 4,
                  fontSize: 11,
                  fontWeight: 500,
                  pointerEvents: "none",
                }}
              >
                {chartModes[idx] === "local" ? "" : "TradingView"}
              </div>
            </div>
          ))}
        </div>
      )}

      {/*  /   */}
      <div style={{ marginTop: 16 }}>
        {error && (
          <div style={{ color: "#fca5a5", marginBottom: 8 }}>{error}</div>
        )}

        {analysis && (
          <div
            style={{
              border: "1px solid #1f2937",
              borderRadius: 8,
              padding: 12,
              background: "#020617",
              fontSize: 13,
            }}
          >
            {/*   analysis    */}
          </div>
        )}
      </div>

      {/* RSI   */}
      {showRsiView && (
        <div
          style={{
            position: "fixed",
            inset: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 9999,
          }}
        >
          <div
            style={{
              position: "relative",
              width: "100%",
              maxWidth: 1000,
              height: "100%",
              maxHeight: "100dvh",
              backgroundColor: "#fff",
              borderRadius: 0,
              overflow: "auto",
              boxShadow: "0 8px 24px rgba(0,0,0,0.25)",
            }}
          >
            <button
              onClick={() => setShowRsiView(false)}
              style={{
                position: "absolute",
                top: 12,
                right: 12,
                border: "none",
                borderRadius: 999,
                padding: "4px 10px",
                fontSize: 12,
                cursor: "pointer",
                background: "#eeeeee",
              }}
            >
              
            </button>

            <RsiGptFrontOnlyDemo />
          </div>
        </div>
      )}
    </div>
  );
}

export default TradingViewAIDashboard;
